.c-tableSorter__content {
  position: relative;
}
.c-tableSorter__loading {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.1);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.c-tableSorter__subcell {
  display: flex;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
}
.c-tableSorter___seefilters-icon {
  cursor: pointer;
}
.c-tableSorter__pagination {
  justify-content: flex-end;
}
.c-tableSorter .header {
  cursor: pointer;
}
.c-tableSorter tr.slideAnimate {
  opacity: 0;
  transform: translateX(-20px);
  animation: slideInFromLeft 0.2s forwards;
}
.highContrast tbody tr:nth-child(odd) {
  background: #000000;
}
.highContrast tbody tr:nth-child(even) {
  background: #3E3832;
}
.highContrast .tabell__tr--valgt td {
  background: #FF9100 !important;
  color: #000000 !important;
}
.highContrast .tabell__tr--valgt td * {
  color: #000000 !important;
}
