.c-progressbar {
  background-color: #C6C2BF;
  width: 100%;
  display: block;
  border-radius: 4px;
}
.c-progressbar__bar {
  border-radius: 4px;
  line-height: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #FFF;
  text-align: center;
  white-space: nowrap;
  transition: width 0.6s ease;
  background-size: 1rem 1rem;
  animation: c-progressbar-animation 1s linear infinite;
}
.c-progressbar__bar-inprogress {
  background-color: #0067C5;
  background-image: linear-gradient(45deg, hsla(0, 0%, 100%, 0.15) 25%, transparent 0, transparent 50%, hsla(0, 0%, 100%, 0.15) 0, hsla(0, 0%, 100%, 0.15) 75%, transparent 0, transparent);
}
.c-progressbar__bar-done {
  background-color: #06893A;
}
.c-progressbar__bar-error {
  background-color: #BA3A26;
}
.highContrast .c-progressbar {
  background-color: #3E3832;
  border: 2px solid #FFF;
}
.highContrast .c-progressbar__bar {
  color: #000000;
}
.highContrast .c-progressbar__bar-inprogress {
  background-color: #66CBEC;
}
.highContrast .c-progressbar__bar-done {
  background-color: #38a161;
}
.highContrast .c-progressbar__bar-error {
  background-color: #BA3A26;
  color: #FFF;
}
.highContrast .c-progressbar__bar-todo {
  color: #FFF;
}
