.c-banner {
  background-color: rgba(255, 167, 51, 0.8);
  position: relative;
}
.c-banner__title {
  margin: 1.5rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  text-align: center;
  font-size: 1.5rem !important;
}
.c-banner__highcontrast-link {
  position: absolute;
  right: 1rem;
  color: #000000;
  background-color: #FFF !important;
  text-decoration: none;
}
.highContrast .c-banner {
  background-color: #000000 !important;
  color: #FFF;
}
.highContrast .c-banner__highcontrast-link {
  color: #FFF;
}
