.highContrast .fieldset,
.highContrast .panel {
  background: #000000 !important;
}
.highContrast .skjemaelement__input {
  background: #000000 !important;
}
.highContrast .alertstripe {
  background: #000000 !important;
  border-width: 2px;
}
.highContrast .nav-veilederpanel {
  background: #000000 !important;
}
.highContrast .knapp--disabled {
  background-color: #3E3832 !important;
}
.highContrast p,
.highContrast code,
.highContrast th,
.highContrast td,
.highContrast section,
.highContrast aside,
.highContrast article,
.highContrast header,
.highContrast footer,
.highContrast li,
.highContrast h1,
.highContrast h2,
.highContrast h3,
.highContrast h4,
.highContrast h5,
.highContrast h6,
.highContrast input,
.highContrast time,
.highContrast figcaption,
.highContrast select,
.highContrast textarea,
.highContrast label {
  color: #FFF;
}
.highContrast a,
.highContrast .link-btn,
.highContrast .fake-link {
  color: #FFBD66 !important;
}
.highContrast a:hover,
.highContrast .link-btn:hover,
.highContrast .fake-link:hover,
.highContrast a:active,
.highContrast .link-btn:active,
.highContrast .fake-link:active,
.highContrast a:focus,
.highContrast .link-btn:focus,
.highContrast .fake-link:focus {
  color: #66CBEC !important;
}
.highContrast a h1,
.highContrast a h2,
.highContrast a h3,
.highContrast a p,
.highContrast a span {
  color: #FF9100 !important;
}
.highContrast a:hover h1,
.highContrast a:active h1,
.highContrast a:focus h1,
.highContrast a:hover h2,
.highContrast a:active h2,
.highContrast a:focus h2,
.highContrast a:hover h3,
.highContrast a:active h3,
.highContrast a:focus h3,
.highContrast a:hover p,
.highContrast a:active p,
.highContrast a:focus p,
.highContrast a:hover span,
.highContrast a:active span,
.highContrast a:focus span {
  color: #66CBEC !important;
}
.highContrast label,
.highContrast input,
.highContrast select,
.highContrast textarea {
  color: #FFF !important;
}
.highContrast .knapp,
.highContrast .knapp.hoved,
.highContrast .knapp.btn-auth,
.highContrast .btn {
  border: 1px solid #FFF;
  color: #FF9100 !important;
  background-image: none;
}
.highContrast .knapp:focus,
.highContrast .knapp.hoved:focus,
.highContrast .knapp.btn-auth:focus,
.highContrast .btn:focus,
.highContrast .knapp:hover,
.highContrast .knapp.hoved:hover,
.highContrast .knapp.btn-auth:hover,
.highContrast .btn:hover,
.highContrast .knapp:active,
.highContrast .knapp.hoved:active,
.highContrast .knapp.btn-auth:active,
.highContrast .btn:active {
  border-color: #ffc40d;
  color: #FFBD66 !important;
  text-decoration: underline;
}
.highContrast *::placeholder {
  color: #FFF;
}
.highContrast .dropdown-menu {
  border: 1px solid #FFF;
}
.highContrast li.dropdown .caret {
  border-top: 4px solid #FF9100;
}
.highContrast li.dropdown:hover .caret,
.highContrast li.dropdown:active .caret,
.highContrast li.dropdown:focus .caret {
  border-top: 4px solid #66CBEC;
}
.highContrast .btn-small {
  background-color: transparent;
  background-image: none !important;
  border: 1px solid #E9E7E7;
}
.highContrast .btn-small:hover,
.highContrast .btn-small:active,
.highContrast .btn-small:focus {
  color: #FFBD66;
}
.highContrast .skjemaelement__input {
  border-width: 2px;
  border-color: #FFF;
}
.highContrast .skjemaelement__input--harFeil {
