.c-file-Other {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  -webkit-backface-visibility: hidden;
}
.c-file-Other .content {
  background-color: #FFF;
  border: solid 1px #C6C2BF;
  box-shadow: 3px 3px 3px lightgray;
}
.c-file-Other .content .extension {
  margin-top: 3rem;
  font-weight: bold;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}
