@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
  .c-flag.type-original {
    display: inline-block;
  }
  .c-flag.type-original img {
    border: 1px solid #B7B1A9;
  }
}
@supports not (-ms-high-contrast: none) {
  /* Non-IE styles here */
  .c-flag.type-original {
    border: 1px solid #B7B1A9;
    display: inline-flex;
  }
}
@keyframes jiggle {
  0% {
    opacity: 0.01;
    transform: scale3d(1, 1, 1);
  }
  1% {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}
.c-flag.type-circle {
  display: inline-flex;
  border: 1px solid #B7B1A9;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}
.c-flag.type-circle img {
  position: absolute;
  margin: auto;
  top: -999px;
  left: -999px;
  bottom: -999px;
  right: -999px;
}
.c-flag.type-circle.size-S {
  width: 16px;
  height: 16px;
  min-width: 16px;
  min-height: 16px;
}
.c-flag.type-circle.size-S img {
  height: 16px;
}
.c-flag.type-circle.size-M {
  width: 25px;
  height: 25px;
  min-width: 25px;
  min-height: 25px;
}
.c-flag.type-circle.size-M img {
  height: 25px;
}
.c-flag.type-circle.size-L {
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
}
.c-flag.type-circle.size-L img {
  height: 40px;
}
.c-flag.type-circle.size-XL {
  width: 70px;
  height: 70px;
  min-width: 70px;
  min-height: 70px;
}
.c-flag.type-circle.size-XL img {
  height: 70px;
}
.c-flag.type-original.size-S img {
  height: 16px;
  min-height: 16px;
  width: auto;
}
.c-flag.type-original.size-M img {
  height: 25px;
  min-height: 25px;
  width: auto;
}
.c-flag.type-original.size-L img {
  height: 40px;
  min-height: 40px;
  width: auto;
}
.c-flag.type-original.size-XL img {
  height: 70px;
  min-height: 70px;
  width: auto;
}
.c-flaglist {
  display: flex;
  flex-wrap: nowrap;
}
.c-flaglist.wrap {
  flex-wrap: wrap;
}
.c-flaglist.animate .c-flag {
  opacity: 0.01;
  animation: jiggle 0.5s forwards;
}
