.c-multipleSelect .c-multipleOption {
  padding: 10px;
}
.c-multipleSelect .c-multipleOption.selected {
  background: #66CBEC;
}
.c-multipleSelect .c-multipleOption.selected label {
  color: #000000 !important;
}
.c-multipleSelect .c-multipleOption.focused {
  background: aliceblue;
}
.c-multipleSelect .c-multipleOption.focused label {
  color: #000000 !important;
}
.c-multipleSelect .c-multipleOption .skjemaelement {
  margin-bottom: 0rem !important;
}
.c-multipleSelect .c-multipleOption img {
  border: 1px solid #78706A;
}
.c-multipleSelect .multipleSelect__multi-value {
  border-radius: 20px;
  border: 1px solid #3E3832;
  padding: 0.25rem;
}
.c-multipleSelect .multipleSelect__multi-value:focus {
  outline: 0;
  box-shadow: 0 0 0 3px #254b6d;
}
.c-multipleSelect .multipleSelect__multi-value__remove {
  border-radius: 20px;
  border: 1px solid #3E3832;
  margin-left: 0.25rem;
}
.c-multipleSelect .c-multipleOption-label {
  color: #000000;
  font-style: normal;
}
.c-multipleSelect .multipleSelect__control {
  border: 1px solid #3E3832;
}
.c-multipleSelect .multipleSelect__indicator-separator {
  background-color: #3E3832;
}
.c-multipleSelect .c-multipleSelect-multipleValueRemove {
  border: 1px solid #3E3832;
  border-radius: 20px;
  width: 24px;
  height: 24px;
  text-align: center;
}
.c-multipleSelect .c-multipleSelect-multipleValueRemove:hover {
  background-color: #FFF;
}
.c-multipleSelect.skjemaelement__feilmelding .multipleSelect__control {
  border: 1px solid #BA3A26;
}
.highContrast .c-multipleSelect .multipleSelect__control {
  border-width: 2px;
  border-color: #FFF;
}
.highContrast .c-multipleSelect .multipleSelect__control {
  background: black !important;
}
.highContrast .c-multipleSelect .c-multipleOption {
  background: #000000;
}
.highContrast .c-multipleSelect .c-multipleOption.selected {
  background: #0067C5;
  color: #000000 !important;
  font-weight: bold;
}
.highContrast .c-multipleSelect .c-multipleOption.focused {
  background: #FFBD66;
  color: #000000 !important;
  font-weight: bold;
}
.highContrast .c-multipleSelect.skjemaelement__feilmelding .multipleSelect__control {
  border-width: 2px;
  border-color: #BA3A26;
}
.highContrast .c-multipleSelect .multipleSelect__multi-value {
  border: 1px solid #FFF;
  background-color: #000000;
}
.highContrast .c-multipleSelect .multipleSelect__multi-value__label {
  color: #FFF;
}
.highContrast .c-multipleSelect .multipleSelect__group {
  background-color: #000000;
}
.c-multipleOption img,
.c-multipleValue img {
  width: 50px;
  height: 30px;
  margin-right: 0.7rem;
}
